import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  base: '/tongqi/',
  routes: [
    {
		path: '/',
		redirect: '/Home'
	},
    {
      path: '/',
      component: resolve => require(['@/components/ContentPage'], resolve),
     	 children: [
          {
              path: '/Home',
              name: 'Home',
              component: resolve => require(['@/pages/Home'], resolve)
          },
          {
            path: '/Intro',
            name: 'Intro',
            component: resolve => require(['@/pages/Intro'], resolve)
          },
          {
            path: '/Activity',
            name: 'Activity',
            component: resolve => require(['@/pages/Activity'], resolve)
          },
          {
            path: '/Activity',
            name: 'Activity',
            component: resolve => require(['@/pages/Activity'], resolve)
          },
          {
            path: '/Book',
            name: 'Book',
            component: resolve => require(['@/pages/Book'], resolve)
          },
          {
            path: '/Acripts',
            name: 'Acripts',
            component: resolve => require(['@/pages/Acripts'], resolve)
          },
          {
            path: '/ActivityDetails',
            name: 'ActivityDetails',
            component: resolve => require(['@/pages/ActivityDetails'], resolve)
          },
          {
            path: '/Move',
            name: 'Move',
            component: resolve => require(['@/pages/Move'], resolve)
          },
          {
            path: '/AcriptsDetails',
            name: 'AcriptsDetails',
            component: resolve => require(['@/pages/AcriptsDetails'], resolve)
          },
          {
            path: '/VimeImage',
            name: 'VimeImage',
            component: resolve => require(['@/pages/VimeImage'], resolve)
          }

      ]
    }
  ]
})



